<template>
  <div class="userUpdate">
    <div class="container mt-5">
      <h2 class="mb-4">  </h2>
      <form @submit.prevent="userUpdate">
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label for="username"><sup>*</sup></label>
            <input
              type="text"
              class="form-control"
              :value="userInfo.id"
              readonly
            />
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label for="password"> <sup>*</sup></label>
            <input
              type="password"
              class="form-control"
              placeholder="  "
              v-model="userInfo.pwd"
            />
          </div>
        </div>
        <p class="form-label">
          - 8~16,  /, , (~!@#$%^&*)  
        </p>
        <!--   -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label for="confirmPassword"> <sup>*</sup></label>
            <input
              type="password"
              class="form-control"
              placeholder="   "
              v-model="newPasswordConfirm"
            />
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label class="bold"><sup>*</sup></label>

            <input
              type="text"
              class="form-control"
              :value="userInfo.name"
              readonly
            />
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label class="bold"><sup>*</sup></label>
            <input
              type="text"
              class="form-control"
              placeholder="'-'  "
              v-model="phoneNum"
              maxlength="11"
            />
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label for="birthday"></label>
            <input
              type="date"
              class="form-control"
              v-model="userInfo.birth"
              
            />
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label class="bold"><sup>*</sup></label>
            <input
              type="text"
              class="form-control"
              placeholder="  ( : email@example.com)"
              v-model="userInfo.email"
              @change="emailState(userInfo.email)"
            />
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <label class="bold"><sup>*</sup></label>
            <div>
              <AddrsPost v-on:userEvent="updateUser" />
            </div>
            <input
              type="text"
              class="form-control"
              placeholder=""
              id="postcode"
              v-model="userInfo.postcode"
              readonly
            />
          </div>
        </div>
        <div class="col-md-12 col-lg-6">
          <div class="form-item w-100">                        
          </div>
        </div>
        <!--  -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <input
              type="text"
              class="form-control"
              placeholder=""
              id="address"
              v-model="userInfo.address"
              readonly
            />
            <input
              type="text"
              class="form-control my-3"
              placeholder=""
              id="address_detail"
              v-model="userInfo.address_detail"
            />
          </div>
        </div>
        <!--   -->
        <div class="row">
          <div class="form-item col-lg-6 my-3">
            <button
              type="submit"
              class="btn btn-primary2 border-secondary py-3 px-4 text-uppercase w-100"
              @click="userUpdate()"
            >
              
            </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AddrsPost from "@/components/AddrsPost.vue";

export default {
  name: "UserUpdate",
  data() {
    return {
      userInfo: {
        id: this.$store.state.userStore.id, //  ( )
        name: this.$store.state.userStore.name, //  ( )
        pwd: "", //  
        phone: this.$store.state.userStore.phone, // 
        birth: this.$store.state.userStore.birth_dt, //  ( )
        email: this.$store.state.userStore.email, // 
        postcode: this.$store.state.userStore.post_cd, // 
        address: this.$store.state.userStore.addr, // 
        address_detail: this.$store.state.userStore.addrdt, // 
      },
      userList: [],
      check: false,
      newPasswordConfirm: "", //   
    };
  },
  created() {},
  computed: {
    phoneNum: {
      get() {
        return this.userInfo.phone;
      },
      set(newValue) {
        if (newValue.length == 11) {
          this.userInfo.phone = newValue.replace(
            /^(\d{3})(\d{4})(\d{4})$/,
            `$1-$2-$3`
          );
        }
      },
    },
  },
  components: { AddrsPost },
  methods: {
    validation() {
      if (this.userInfo.pwd == "") {
        alert(" .");
        return false;
      }
      if (this.newPasswordConfirm == "") {
        alert("  .");
        return false;
      }
      if (this.userInfo.phone == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.email == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.postcode == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.address_detail == "") {
        alert(" .");
        return false;
      }
      if (!/^[a-zA-Z0-9~!@#$%^&*]{8,16}$/g.test(this.userInfo.pwd)) {
        alert(
          " 8~16,  /, , (~!@#$%^&*)  ."
        );
        return false;
      }
      if (!(this.userInfo.pwd == this.newPasswordConfirm)) {
        alert("  .");
        return false;
      }
      return true;
    },
    getCode(zonecode, roadAddress) {
      this.userInfo.postcode = zonecode;
      this.userInfo.address = roadAddress;
    },
    //  
    emailState(value) {
      if (
        value.indexOf("@") == -1 &&
        (value.indexOf(".com") == -1 || value.indexOf(".net") == -1)
      ) {
        Swal.fire(" . \n   .");
        this.userInfo.email = "";
      }
    },
    //   API
    updateUser(value) {
      console.log(value);
      this.userInfo.address = value.addrr;
      this.userInfo.postcode = value.zip;
    },
    //   
    async userUpdate() {
      if (!this.validation()) return;

      let data = {
        param: {
          pwd: this.userInfo.pwd,
          phone: this.userInfo.phone,
          email: this.userInfo.email,
          post_cd: this.userInfo.postcode,
          user_type: this.userInfo.type,
          addr: this.userInfo.address,
          addrdt: this.userInfo.address_detail,
          birth_dt: this.userInfo.birth,
        },
      };
      let result = await axios
        .post(
          `/api/user/userUpdate/${this.$store.state.userStore.mem_no}`,
          data
        )
        .catch((err) => console.log(err));
      let info = result.data.affectedRows;
      if (info > 0) {
        alert("  .");
      } else {
        alert("  .");
      }
      //      
      // axios      
    },
  },
};
</script>

<style scoped>
.center {
  justify-content: center;
}

sup {
  color: red;
}

.bold {
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.margin-right {
  margin-right: 0.5rem;
}

input::placeholder {
  color: #cccccc;
}

input:read-only {
  background-color: #eeeeee;
}

.breadcrumb-item + .breadcrumb-item::before {
  float: left;
  padding-right: 0.5rem;
  color: #fd7e14;
  content: var(--bs-breadcrumb-divider, "/");
  /* rtl: var(--bs-breadcrumb-divider, "/") */
}
</style>