        <caption>  </caption>
        <colgroup>
            <col width="12%"><col width="38%"><col width="12%"><col width="">
        </colgroup>
        <tbody>
        <tr>
            <th><label for="sendName"> </label></th>
            <td><input type="text" class="txtInp" name="buyname" maxlength="32" v-model="deliInfo.name" id="sendName" ref="deliNameFocus"></td>
        </tr>
        <tr>
            <th></th>
            <td colspan="3">
                <!-- -->
                <p>
                  <input type="text" name="buyZip" v-model='deliInfo.post_cd' readonly title="" class="txtInp focusOn" style="width:100px; margin-right:1rem;" ref="addrFocus">
                  <AddrsPost v-on:userEvent="updateUser"/>
    </p>
    <p class="tPad05">
        <input name="buyAddr1" type="text" class="txtInp" style="width:420px;" v-model='deliInfo.addr' title="  " readonly>
    </p>
                <p><input name="buyAddr2" type="text" class="txtInp" style="width:440px;" v-model="deliInfo.addrdt" title=" " @change="detailAddr"></p>
            </td>
        </tr>
        <tr>
            <th><label for="hp01"></label></th>
            <td>
                <p>
                  <input type="text" class="txtInp" name="buyhp1" v-model="phoneNum" title="   " maxlength="11" id="hp01" ref="deliPhoneFocus">
                </p>
                </td>
        </tr>
        </tbody>
    </table>
        <!--    -->
        <DiscountAndFinalPrice :list="paymentList" :point="userInfo.point" :fee="fee" @selected="updateSale1" @newPoint="updateSale2" @finalPrice="updateSale3" @couponPrice="updateSale4"/>
        <div class="orderNotiV21 tMar30">
            <p class="txtArrow">            .</p>
            <p class="txtArrow tMar15">
                    (3)  .
            </p>
            <div class="chkAgreeV21 tMar30"><label for="agreeAll"><input type="checkbox" id="agreeAll" v-model="checkItem" ref="checkTerms">    .</label></div>
            <!--:true-value="trueValueMethod()" :false-value="falseValueMethod()"-->
					</div>
          <div class="ct tPad30 bPad20" id="nextbutton1" name="nextbutton1" style="display: flex;justify-content: center;">
						<button class="lMar10 btn btnB2 btnWhite2 btnW220" @click="backBtn"><em class="gryArr02"> </em></button>
						<button name="btnPay" id="btnPay" class="lMar10 btn btnB2 btnRed btnW220" @click="PaymentBtn"></button>
					</div>
  </template>

<script>
import TotalOrderPrice from '@/components/totalOrderPrice.vue';
import DiscountAndFinalPrice from '@/components/DiscountAndFinalPrice.vue';
import AddrsPost from '@/components/AddrsPost.vue';
import axios from 'axios';
import emailjs from '@emailjs/browser';

export default {
  data() {
      return {
          paymentList: [], // 
          userInfo: {}, // 
          deliInfo:{}, // 
          point:0, // 
          couponSelected:{}, //
          finalPrice : 0, //
          checkItem : false, //
          fee : 0,  //
          total_prices : 0, // ()
          couponPrice : 0 //
      };
  },
  computed:{
    phoneNum: {
      get() {
        return this.deliInfo.phone
      },
      set(newValue) {
        if(newValue.length ==11){
          this.deliInfo.phone = newValue.replace(/^(\d{3})(\d{4})(\d{4})$/, `$1-$2-$3`)
        }
      }
    },
    email1: {
      get() {
        return this.deliInfo.email
      },
      set(newValue) {
            this.deliInfo.phone = newValue.split('@')
      }
    },
    //DB   
    updatePoint(){
      return this.userInfo.point - this.point + 1;
    },
    //  
    accountPoint(){
        let persent = this.userInfo.rwd_pct;
        console.log(persent)
        return this.finalPrice * (persent /100);
      },
  },
  created() {
    //  
    this.paymentList = JSON.parse(sessionStorage.getItem("payList"));
    this.getUserInfo();
    if (this.paymentList == []) {
      this.$router.go(-1);
    };
  },
  mounted(){
    //  
    for(let list of this.paymentList){
      this.finalPrice += list.total_price
      this.total_prices += list.total_price
    }
    this.fee = this.total_prices >= 50000 ? 0 : 3000;
    

  },
  methods: {
      // ( )
      async getPaymentList() {
          let result = await axios.get(`/apiproduct/cart/${this.$store.state.userStore.mem_no}`)
              .catch(err => console.log(err));
          console.log(result);
          let list = result.data;
          this.paymentList = list;
        },
        //
        async getUserInfo() {
          let result = await axios.get(`/apiuser/userInfo/${this.$store.state.userStore.mem_no}`)
          .catch(err => console.log(err));
          console.log(result);
          let list = result.data;
          this.userInfo = list;
          this.deliInfo = { ...this.userInfo }
      },
      // 
      updateUser(value){
        console.log(value)
        this.deliInfo.addr = value.addrr;
        this.deliInfo.post_cd = value.zip;
      },
      //  
      emailState(value){
        if(value.indexOf('@')==-1 && (value.indexOf('.com')==-1 || value.indexOf('.net')==-1)){
            Swal.fire(" . \n   .");
            this.userInfo.email = '';
        }
      },
      //  
      phoneState(value){
        const phoneRule = /^(01[016789]{1})[0-9]{3,4}[0-9]{4}$/;
        if(value != "" && !phoneRule.test(value)){
            Swal.fire("  . \n    .");
            this.deliInfo.phone = '';
        }
      },
      //  
      updateSale1(value){
        this.couponSelected = value;
      },
      //  
      updateSale2(value){
        if(value == null){
          this.point=0;
        }else{
          this.point = value;
        }
      },
      //  
      updateSale3(value){
        this.finalPrice = value;
      },
      //  
      updateSale4(value){
        this.couponPrice = value;
      },
      // 
      async PaymentBtn(){
        //
        if (!this.validation()) return;
        
        IMP.init("imp42374167");

        const data = {
          pg: 'html5_inicis',                         // PG
          pay_method: 'card',                         // 
          merchant_uid: `M${new Date().getTime()}`,   //  
          amount: this.finalPrice,                    //   this.finalPrice
          name: ' ',                        // 
          buyer_name: this.userInfo.name,            //  
          buyer_tel: this.userInfo.phone,            //  
          buyer_email: this.userInfo.email,          //  
          buyer_addr: this.deliInfo.addr + " " +  this.deliInfo.addrdt,  //  
          buyer_postcode: this.deliInfo.post_cd                      //  
        };

        const vue = this;
        IMP.request_pay(data, rsp => { // callback
          console.log(rsp);
          if (rsp.success) {
          
            //
            axios({
              url: '/apiorder/orderList',
              method: "post",
              headers: { "Content-Type": "application/json" },
              data:  vue.payData(rsp)

            }).then((data) => {
              //   API  
              console.log(data.data[0].ord_no);
              if(data.data[1].affectedRows > 0 && data.data[5].affectedRows > 0){
                 this.sendRequestEmail(data.data[0].ord_no);

                console.log(" ");
                this.$router.push({path:'/completePayment', query : {"no" : data.data[0].ord_no}});
              }else{
                console.log(" ");
                Swal.fire({
                  icon: "error",
                  title: " ",
                  text: "   .",
                  footer: '<a href="#"> </a>'
                });
                this.$router.go(-1);
              }
            })
          } else {
            console.log(" ");
            this.$router.go(-1);
            // this.$router.push({path:'/'});
          }
        });
    },
    //  
    payData(rsp){
      const orderData = {
        "OrderTable" : {
          deli_addr : this.deliInfo.addr,
          deli_addrdt : this.deliInfo.addrdt,
          deli_cost : this.fee,
          ord_dt : this.getDate(),
          rcv_name : this.deliInfo.name,
          rcv_email : this.deliInfo.email,
          rcv_phone : this.deliInfo.phone,
          status : 1,
          total_price : this.total_prices,
          used_point : this.point,
          total_payment : this.finalPrice,
          payment_no : rsp.imp_uid,
          ord_no : rsp.merchant_uid,
          accu_pnt : this.roundNum(this.accountPoint),
          cpn_disc : this.couponPrice,
          mem_no : this.$store.state.userStore.mem_no,
          cpn_no : this.couponSelected.cpn_no,
          rcv_postcode : this.deliInfo.post_cd
        },
        "paymentList" : {
          paymentLists : this.paymentList,
          ord_no : rsp.merchant_uid
        },
        "cartList" : {
          cart_cds : this.paymentList
        },
        "userPoint" : {
            point : this.roundNum(this.userInfo.point + this.accountPoint - this.point),
            mem_no : this.$store.state.userStore.mem_no
        },
        //userInfo   (  )
        "usergrade" : {
          used_payment : this.userInfo.used_payment + this.finalPrice,
          mem_no : this.$store.state.userStore.mem_no
        },
        "usercoupon" : {
            poss_no : this.couponSelected.poss_no,
            // end_dt : this.getDate(),
            status : 1,
            cpnused_dt : this.getDate()
        },
        "deliveryAdd" : {
            deli_no : `D${new Date().getTime()}`,
            ship_no : `S${new Date().getTime()}`,
            status : 0,
            ord_no : rsp.merchant_uid,
            ship_comp : '',
            arr_dt : this.getWeekAgo()
        }
      } 
      console.log(JSON.stringify(orderData))
      return orderData
    },
    //  
    getDate() {
      let res = null;
      let date = new Date();
      let y = date.getFullYear();
      let m = ("0" + (date.getMonth() + 1)).slice(-2);
      let d = ("0" + date.getDate()).slice(-2);
      let h = ("0" + date.getHours()).slice(-2);
      let mi = ("0" + date.getMinutes()).slice(-2);
      let s = ("0" + date.getSeconds()).slice(-2);

      res = `${y}-${m}-${d} ${h}:${mi}:${s}`;
      
      return res;
    },
    // 
    backBtn(){
      this.$router.go(-1);
    },
    //  
    roundNum(num){
      return Math.round(num);
    },
    //   
    validation(){
      if (this.userInfo.email == "" || this.userInfo.email == null) {
        Swal.fire(" .");
        this.$refs.emailFocus.focus();
        return false;
      }
      if (this.deliInfo.name == "" || this.deliInfo.name == null) {
        Swal.fire(" .");
        this.$refs.deliNameFocus.focus();
        return false;
      }
      if (this.deliInfo.post_cd == null || this.deliInfo.addr == null || this.deliInfo.addrdt == null) {
        Swal.fire(" .");
        this.$refs.addrFocus.focus();
        return false;
      }
      if (this.deliInfo.phone == "" || this.deliInfo.phone == null) {
        Swal.fire("  .");
        this.$refs.deliPhoneFocus.focus();
        return false;
      }
      if(!this.checkItem){
          Swal.fire(" .");
          this.$refs.checkTerms.focus();
          return false;
      }
      return true;
    },
    //  
    imgMod(img){
      return 'img/' + img.substring(21)
    },
    // 
    sendRequestEmail(ord_no) {
      const payload = {
        // from_name: this.from_name,
        // to_name: this.to_name,
        // message: this.message,
        // form_name: this.form.name,
        // form_title: this.form.title,
        // form_content: this.form.content,
        form_time: new Date(),
        ord_nos : ord_no,
        paymentList : this.paymentList, // 
        userInfo: this.userInfo, // 
        deliInfo:this.deliInfo, // 
        point:this.point, // 
        couponSelected:this.couponSelected, //
        finalPrice : this.finalPrice, //
        fee : this.fee,  //
        total_prices : this.total_prices, // ()
        couponPrice : this.couponPrice //
      }
      emailjs.send('service_cg36xv8', 'template_iky19ki', payload, '_W_PmzEjAaLrqeshH')
        .then((res) => {
            console.log(res)
        }, (error) => {
            console.log(error)
        })
    },
    // 
    getWeekAgo(){
            let weekAgo = new Date();
            weekAgo.setDate(weekAgo.getDate()+7);

            let res = null;
            let y = weekAgo.getFullYear();
            let m = ("0" + (weekAgo.getMonth() + 1)).slice(-2);
            let d = ("0" + weekAgo.getDate()).slice(-2);
            let h = ("0" + weekAgo.getHours()).slice(-2);
            let mi = ("0" + weekAgo.getMinutes()).slice(-2);
            let s = ("0" + weekAgo.getSeconds()).slice(-2);

            res = `${y}-${m}-${d} ${h}:${mi}:${s}`;
            return res;
        }
},
  components: { TotalOrderPrice,DiscountAndFinalPrice, AddrsPost }
}
</script>
  
