<!-- userJoinView.vue -->
<template>
  <div class="userJoin">
    <head>
      <meta charset="UTF-8" />
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
      <title></title>
      <link
        href="https://stackpath.bootstrapcdn.com/bootstrap/4.5.2/css/bootstrap.min.css"
        rel="stylesheet"
      />
    </head>
    <body>
      <div class="container mt-5">
        <h2 class="mb-4"></h2>
        <form>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label for="username"><sup>*</sup></label>
              <input @change="this.getUserList()"
                type="text"
                class="form-control"
                placeholder=" "
                id="id"
                v-model="userInfo.id"
                autofocus
              />
              <p class="form-label">
                - 5~20  ,   .
              </p>

              <button
                type="button"
                class="btn btn-primary mt-2"
                @click="checkID"
              >
                  
              </button>
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label for="password"><sup>*</sup></label>
              <input
                type="password"
                class="form-control"
                placeholder=" "
                id="pw"
                v-model="userInfo.pw"
              />
            </div>
          </div>
          <p class="form-label">
            - 8~16,  /, , (~!@#$%^&*)  
          </p>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label for="confirmPassword"> <sup>*</sup></label>
              <input
                type="password"
                class="form-control"
                placeholder="   "
                id="pw_confirm"
                v-model="userInfo.pw_confirm"
              />
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label class="bold"><sup>*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder=" "
                id="name"
                v-model="userInfo.name"
              />
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label class="bold"><sup>*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder="'-'  "
                id="phone"
                v-model="userInfo.phone"
              />
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label for="birthday"></label>
              <input
                type="date"
                class="form-control"
                id="birth"
                v-model="userInfo.birth"
              />
            </div>
          </div>
          <div class="form-group">
            <label></label>
            <div class="form-check">
              <input
                class="form-check-input"
                type="radio"
                name="gender"
                id="male"
                value="M"
                v-model="userInfo.gen"
              />
              <label class="form-check-label" for="male"></label>
            </div>
            <div class="form-check">
              <input
                class="form-check-input"
                type="radio"
                name="gender"
                id="female"
                value="F"
                v-model="userInfo.gen"
              />
              <label class="form-check-label" for="female"></label>
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <label class="bold"><sup>*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder="  ( : email@example.com)"
                id="email"
                v-model="userInfo.email"
              />
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <div class="form-item w-100">
                <label class="bold"><sup>*</sup></label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  id="postcode"
                  v-model="userInfo.postcode"
                  readonly
                />
              </div>
            </div>
            <div class="col-md-12 col-lg-6">
              <div class="form-item w-100">
                <br /><br />
                <div>
                  <AddrsPost v-on:userEvent="updateUser" />
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <input
                type="text"
                class="form-control"
                placeholder=""
                id="address"
                v-model="userInfo.address"
                readonly
              />

              <input
                type="text"
                class="form-control my-3"
                placeholder=""
                id="address_detail"
                v-model="userInfo.address_detail"
              />
            </div>
          </div>
          <div class="row">
            <div class="form-item col-lg-6 my-3">
              <button
                type="button"
                class="btn btn-primary2 border-secondary py-3 px-4 text-uppercase w-100"
                @click="userInsert()"
              >
                
              </button>
            </div>
          </div>
        </form>
        {{ userInfo }}
      </div>
    </body>
  </div>
</template>

<script>
import axios from "axios";
import AddrsPost from "@/components/AddrsPost.vue";

export default {
  name: "SignUp",
  data() {
    return {
      userInfo: {
        id: "",
        pw: "",
        pw_confirm: "",
        name: "",
        phone: "",
        birth: null,
        gen: "",
        email: "",
        postcode: "",
        address: "",
        address_detail: "",
        point: "",
        token: "",
        mem_status: 2,
        grd_no: 1
      },
      userList: [],
      check: false,
    };
  },
  components: { AddrsPost },
  methods: {
    validation() {
      if (this.userInfo.id == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.pw == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.pw_confirm == "") {
        alert("  .");
        return false;
      }
      if (this.userInfo.name == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.phone == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.email == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.postcode == "") {
        alert(" .");
        return false;
      }
      if (this.userInfo.address_detail == "") {
        alert(" .");
        return false;
      }
      if (!/^(?=.*[a-z])[a-z0-9]{5,20}$/g.test(this.userInfo.id)) {
        alert(" 5~20,  (),   .");
        return false;
      }
      if (!/^[a-zA-Z0-9~!@#$%^&*]{8,16}$/g.test(this.userInfo.pw)) {
        alert(
          " 8~16,  /, , (~!@#$%^&*)  ."
        );
        return false;
      }
      if (!(this.userInfo.pw == this.userInfo.pw_confirm)) {
        alert("  .");
        return false;
      }
      if (!this.check) {
        alert("  .");
        return false;
      }

      return true;
    },
    // openPostcode() {
    //     new window.daum.Postcode({
    //         oncomplete : (data) => {
    //             this.userInfo.postcode = data.zonecode;
    //             this.userInfo.address = data.roadAddress;
    //         }
    //     }).open();
    // },
    getCode(zonecode, roadAddress) {
      this.userInfo.postcode = zonecode;
      this.userInfo.address = roadAddress;
    },
    async getUserList() {
      let result = await axios
        .get("/api/user/checkUser/"+ this.userInfo.id)
        .catch((err) => console.log(err));
      this.userList = result.data;
      console.log(this.userList);
    },
    checkID() {
      if (this.userInfo.id == "") {
        alert(" .");
        return false;
      } else {
        console.log(this.userList.id);
        if (this.userInfo.id == this.userList.id) {
          alert("   .");
          return false;
        } else {
          alert("  .");
          this.check = true;
          return true;
        }
      }
    },
    //   API
    updateUser(value) {
      console.log(value);
      this.userInfo.address = value.addrr;
      this.userInfo.postcode = value.zip;
    },
    //  
    async userInsert() {
      if (!this.validation()) return;
      this.userInfo.birthday = this.getDate(this.userInfo.birthday);
      let data = {
        param: {
          id: this.userInfo.id,
          pwd: this.userInfo.pw,
          name: this.userInfo.name,
          phone: this.userInfo.phone,
          email: this.userInfo.email,
          birth_dt: this.userInfo.birth,
          gen: this.userInfo.gen,
          post_cd: this.userInfo.postcode,
          user_type: this.userInfo.type,
          addr: this.userInfo.address,
          addrdt: this.userInfo.address_detail,
          mem_status: this.userInfo.mem_status,
          grd_no: this.userInfo.grd_no,
        },
      };
      console.log(data)
      let result = await axios

        .post("/api/user/userJoin", data)
        .catch((err) => console.log(err));
      let info = result.data.affectedRows;
      if (info > 0) {
        alert(" .");
      }
      this.$router.push({ path: "/" });
    },
    getDate(dates) {
      let res = null;
      if (dates != null) {
        let date = new Date(dates);
        let y = date.getFullYear();
        let m = ("0" + (date.getMonth() + 1)).slice(-2);
        let d = ("0" + date.getDate()).slice(-2);

        res = `${y}-${m}-${d}`;
      }
      return res;
    },
  },
};
</script>

<style scoped>
.center {
  justify-content: center;
}

sup {
  color: red;
}

.bold {
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.margin-right {
  margin-right: 0.5rem;
}

input::placeholder {
  color: #cccccc;
}

input:read-only {
  background-color: #eeeeee;
}

.breadcrumb-item + .breadcrumb-item::before {
  float: left;
  padding-right: 0.5rem;
  color: #fd7e14;
  content: var(--bs-breadcrumb-divider, "/");
  /* rtl: var(--bs-breadcrumb-divider, "/") */
}

</style>