<template>
    <div class="myContent" style="float: none;">
        <div class="titleSection">
			<h3>1:1 </h3>
		</div>
        <div style="text-align: right;">
            <button style="margin: 0 15px 10px 30px; border: 1px solid #ccc;border-radius: 5px;" ></button>
        </div>
        <div class="mySection">
				<fieldset>
					<table class="baseTable">
						<caption> </caption>
							<colgroup>
								 <col width="50"><col width="88"> <col width="88"><col width="50"><col width="88">
							</colgroup>
						<thead>
							<tr>
                                <th scope="col"></th>
								<th scope="col"></th>
								<th scope="col"></th>
								<th scope="col"></th>
								<th scope="col"></th>
							</tr>
						</thead>
                        <!-- {{ reviewList }} -->
						<tbody>
	                        <tr v-for="(table, idx) in tableList" :key="idx">
								<td>{{ table.qst_no }}</td>
								<td>{{ categorys(table.ctg) }}</td>
								<td>{{ table.title }}</td>
                                <td v-if="answer == null"></td><td v-else></td>
                                <td>{{ getDate(table.qst_dt) }}</td>
            				</tr>
                            <tr v-show="qstBtn">
                                <td colspan="7" style="text-align: left;">
                                    <div v-for="(item, index) in radioList" :key="index">
                                        <input type="radio" :id="item.key" v-model="picked" :value="item" />
                                        <label :for="item.key" class="text">{{ item.value }}</label>
                                    </div>
                                </td>
                            </tr>
                            <tr v-show="qstBtn">
                                <td colspan="7" style="text-align: left;">1:1 </td>
                            </tr>
                            <tr v-show="qstBtn">
                                <td style="text-align: right;"></td>
                                <td colspan="6"><textarea cols="80" rows="10" style="width: 90%; height: 6.25em; border: 1px solid #000; resize: none;" v-model="qstList.content" placeholder=" 300  ."></textarea></td>
                            </tr>
                            <tr v-show="qstBtn">
                                <td style="text-align: right;"> </td>
                                <td colspan="6"><textarea cols="80" rows="10" style="width: 90%; height: 6.25em; border: 1px solid #000; resize: none;" v-model="qstList.answer" placeholder=" 300  ." readonly></textarea></td>
                            </tr>
                            <tr style="border-style: hidden;" v-show="qstBtn">
                                <td></td>
                                <td style="text-align: right;" colspan="2"><input type="file" accept="image/*"></td>
                                <td colspan="4"></td>
                            </tr>
                            <tr v-show="qstBtn">
                                <td></td>
                                <td style="text-align: left;" colspan="2"><button style="margin: 0 15px 0 30px; border: 1px solid #ccc;border-radius: 5px;" ></button><button @click="qstRefesh()" style="border: 1px solid #ccc;border-radius: 5px;"></button></td>
                                <td colspan="4"></td>
                            </tr>
	                        <tr v-if="tableList.length == 0">
								<td colspan="6">  .</td>
            				</tr>
						</tbody>
					</table>
				<div class="pageWrapV15 tMar20">
					<div class="paging">
<!--   -->
<Paging :pagination="pagination" v-on:prevPage="prevPage" v-on:nextPage="nextPage"
v-on:firstPage="firstPage" v-on:lastPaging="lastPaging" v-on:changeNowPage="changeNowPage"/>
                    </div>
				</div>
			</fieldset>
		</div>
    </div>
</template>
<script>
import axios from 'axios'
import Paging from '@/components/PagingComponent.vue';

export default{ 
    components : {
            Paging
    },
    data(){
        return{
            checkSearch : "1",
                searched : this.$store.state.userStore.mem_no,
                qstBtn : false, //  
                qstList : { // 
                    ctg : '',
                    title : '',
                    content : '',
                    qst_dt : null,
                    status : '',
                    answer : '',
                    answer_dt : null
                }, 
                picked: "", // 
                radioList: [ // 
                    {
                      key: 0,
                      value: "",
                    },
                    {
                      key: 1,
                      value: "",
                    },
                    {
                      key: 2,
                      value: "",
                    },
                    {
                      key: 3,
                      value: "",
                    }
                ],    

                // 
                tableList : [],
                paging : [],
                pagination : {},
                allSize : 0,  //   
                pageSize : 5, //     
                navSize : 5,  //     
                lastPage : 1,  // Math.ceil(allSize / pageSize)  
                curPage : 1,  //  
                startPage : 1,  //  
                endPage : 1,  //  
        };
    },
    created(){
        this.getTableList();
        this.picked = this.radioList[0];
    },
    watch: {
    picked() {
      console.log("watch : ", this.picked);
    //   this.getSearch();
    },
  },
    methods:{
            //  
            async getTableList(curPage) {
                curPage = this.judgePage(curPage);
                if (!curPage || curPage <= 0) 
                curPage = this.startPage;
                let gap = curPage%this.navSize === 0 ?  this.navSize - 1 : curPage%this.navSize - 1;
                this.startPage = this.judgePage(curPage - gap);
                this.endPage = this.startPage + this.navSize - 1;
                await this.getTableCount(curPage);
                this.curPage = curPage;
                let data = {
                    param : {
                        limit : this.pageSize,
                        offset : (curPage - 1) * this.pageSize,
                        checkSearch : this.checkSearch,
                        searched : this.$store.state.userStore.mem_no,

                    }
                }
                let result = await axios.post("/api/user/queryList", data)
                                        .catch(err => console.log(err));
                console.log(result);
                this.tableList = result.data;
            },
            //     
            async getTableCount() {
                let data = {
                    param : {
                        checkSearch : this.checkSearch,
                        searched : this.$store.state.userStore.mem_no
                    }
                }
                let result = await axios.post(`/api/user/queryListCount`, data) 
                                        .catch(err => console.log(err));
                this.allSize = result.data[0].count;
                this.lastPage = Math.ceil(this.allSize / this.pageSize);
                this.pagination = {
                                lastPage : this.lastPage, 
                                startPage : this.startPage, 
                                endPage : this.endPage, 
                                curPage : this.curPage
                                }
            },
            judgePage(page) {
                if (!page || page <= 0) 
                page = 1
                else if (page > this.lastPage) 
                page = this.lastPage
                return page
            },

            // 
            prevPage(){
                this.getTableList(--this.curPage);
            },

            nextPage(){
                this.getTableList(++this.curPage);
            },

            firstPage(){
                this.curPage = 1;
                this.getTableList(this.curPage);
            },

            lastPaging(){
                this.curPage = this.lastPage;
                this.getTableList(this.curPage);
                
            },
            
            changeNowPage(page){
                this.curPage = page
                this.getTableList(this.curPage);
            },
             // 
            getDate(days) {
                let res = null;
                let date = new Date(days);
                let y = date.getFullYear();
                let m = ("0" + (date.getMonth() + 1)).slice(-2);
                let d = ("0" + date.getDate()).slice(-2);

                res = `${y}${m}${d}`;

            return res;
            },
            // 
            reviewShow(prdt_cd, ord_no){
                if(this.qstBtn == true){
                    this.qstBtn = false;
                    this.reviewList.prdt_cd = ""
                    this.reviewList.ord_no = ""
                }else if(this.qstBtn == false){
                    this.qstBtn = true;
                    this.reviewList.prdt_cd = prdt_cd
                    this.reviewList.ord_no = ord_no
                }
            },
            //  
            getDateToday() {
              let res = null;
              let date = new Date();
              let y = date.getFullYear();
              let m = ("0" + (date.getMonth() + 1)).slice(-2);
              let d = ("0" + date.getDate()).slice(-2);
            
              res = `${y}-${m}-${d}`;
            
              return res;
            },
            // 
            categorys(no){
                if(no == 0){
                    return "";
                }else if(no == 1){
                    return ""
                }else if(no == 2){
                    return ""
                }else if(no == 3){
                    return ""
                }
            },
            //  
            /*async insertReview(){
                let data = {
                    param : {
                        content : this.reviewList.content,
                        rating : this.reviewList.rating,
                        prdt_cd : this.reviewList.prdt_cd,
                        mem_no : this.reviewList.mem_no,
                        ord_no : this.reviewList.ord_no,
                        rv_dt : this.reviewList.rv_dt
                    }
                }

                if(this.reviewList.content == ""){
                    Swal.fire({
                      icon: "error",
                      title: "  . <br>    ."
                    });

                    return 
                }

                let result = await axios.post(`/api/product/review`, data) 
                                        .catch(err => console.log(err));
                let info = result.data.affectedRows;
                if (info > 0) {
                  alert(" .");
                  this.reviewShow(this.reviewList.prdt_cd, this.reviewList.ord_no)
                  this.getTableList();
                }else{
                  alert(" .");
                  this.reviewShow(this.reviewList.prdt_cd, this.reviewList.ord_no)
                }
            },*/
            //  
            /*async reviewDel(prdt_cd,ord_no){
                let data = {
                    prdt_cd : prdt_cd,
                    ord_no : ord_no
                }
                let result = await axios.post(`/api/product/reviewDelete`, data) 
                                        .catch(err => console.log(err));
                let info = result.data.affectedRows;
                if (info > 0) {
                  alert(" .");
                  this.getTableList();
                }else{
                  alert(" .");
                }
                
            },*/
            //  
            qstRefesh(){
                this.reviewList.content = ""
            }
        }
    }
</script>

